.buttons {
  display: flex;
  gap: 1.5rem;
}

button{
  width: 10rem;
  height: 2rem;
  border-radius: 10px;
  border: none;
}

main {
  margin-top: 1rem;
  border-radius:20px;
  background-color: #3eb489;
  width: 80vw;
  height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.ending > h3{
  font-size: 4rem;
  padding: 0;
  margin: 0 0 2rem 0;
}